// react
import React, { Component } from 'react';

// react-bootstrap
import {
  Button,
  Clearfix,
  Glyphicon,
  Modal,
  Panel,
} from 'react-bootstrap';

// custom
import './style.css';
import { allLevels, allTunnels } from './lib/data';
import { goAll } from './lib/test';
import TunnelModal, { Tunnel } from './comps/TunnelModal';
import Nameko from './comps/Nameko';
import NamekoModal from './comps/NamekoModal';

const copyNamekos = namekos => {
  namekos = JSON.parse(JSON.stringify(namekos));
  namekos.forEach(n => {
    n.skills = n.skills || [];
    n.usedSkills = [ ...n.skills ];
  });
  return namekos;
};

class App extends Component {
  constructor(props) {
    super(props);

    const allNamekos = copyNamekos(allLevels[0].namekos);
    //const allNamekos = copyNamekos(allLevels[3].namekos);

    this.state = {
      forceUpdate: {},
      allTunnels,
      tunnel: allTunnels[1],
      levels: allLevels,
      allNamekos,
      selectedNamekos: allNamekos.slice(0, 7),
      //selectedNamekos: allNamekos.slice(),
      results: [],
      tOpened: false,
      nOpened: false,
      lOpened: false,
      percentage: 0,
    };

    this.useWorker = !!window.Worker;
    //this.useWorker = false;
  }

  componentDidMount() {
    if (this.useWorker) {
      this.worker = new Worker('worker.js');
      this.worker.onmessage = e => {
        const { type, results } = e.data;
        if (type === 'percentage') {
          this.setState({ percentage: results });
        } else if (type === 'results') {
          console.log('DONE!', `end: ${new Date()}`);
          this.setState({ percentage: 0, results, lOpened: false });
        } else {
          console.log('ERROR!', `end: ${new Date()}`, results); // TODO handle errors
          this.setState({ percentage: 0, results: [], lOpened: false });
        }
      };
    }
  }

  updateUI() {
    this.setState({ forceUpdate: {} });
  }

  onSelectTunnel(tunnel) {
    this.setState({ tunnel });
  }

  onSelectNameko(nameko) {
    const { selectedNamekos } = this.state;
    const idx = selectedNamekos.indexOf(nameko);

    if (idx === -1) {
      selectedNamekos.push(nameko);
    } else {
      selectedNamekos.splice(idx, 1);
    }

    this.updateUI();
  }

  onClickNamekoAction(nameko, type, value) {
    if (type === 'skill') {
      const { usedSkills } = nameko;
      const idx = usedSkills.indexOf(value);
      if (idx === -1) {
        usedSkills.push(value);
      } else {
        usedSkills.splice(idx, 1);
      }
    } else if (type === 'sub_value') {
      if (nameko.values[value] <= 5) {
        return;
      }
      nameko.values[value] -= 5;
    } else if (type === 'add_value') {
      nameko.values[value] += 5;
    }
    this.updateUI();
  }

  onClickLevel(level) {
    this.setState({
      allNamekos: copyNamekos(level.namekos),
      selectedNamekos: [],
    });
  }

  onClickRun() {
    console.log(' >> onClickRun', `start: ${new Date()}`, `worker: ${this.useWorker}`);

    this.setState(
      { lOpened: true },
      () => {
        const { tunnel, selectedNamekos } = this.state;

        const namekos = selectedNamekos.map(n => ({
          ...n,
          skills: n.usedSkills,
        }));

        if (this.useWorker) {
          this.worker.postMessage({ tunnel, namekos });
        } else {
          const results = goAll(tunnel, namekos);
          console.log('DONE!', `end: ${new Date()}`);
          this.setState({ results, lOpened: false });
        }
      },
    );

  }

  showTModal(tOpened) {
    this.setState({ tOpened });
  }

  showNModal(nOpened) {
    this.setState({ nOpened });
  }

  render() {
    const { tunnel, allTunnels, levels, allNamekos, selectedNamekos, results, tOpened, nOpened, lOpened } = this.state;

    return (
      <div className='app'>
        <Tunnel
          tunnel={tunnel}
          isSelected={false}
          onOpenModal={() => this.showTModal(true)}
        />
        <Panel>
          <Panel.Heading>
            {`選擇了 ${selectedNamekos.length} 隻菇菇`}
            <Button onClick={() => this.showNModal(true)}>選擇菇菇</Button>
          </Panel.Heading>
          <Panel.Body>
            {(() => {
              const comps = [];

              selectedNamekos.forEach((nameko, idx) => {
                comps.push(<Nameko
                  key={idx}
                  nameko={nameko}
                />);

                if (idx % 3 === 2) {
                  comps.push(<Clearfix key={`_${idx}`} visibleXsBlock />);
                }
              });

              return comps;
            })()}
          </Panel.Body>
        </Panel>
        <Panel>
          {/*
          {results.map((result, idx) => {
          */}
          {results.slice(0, 100).map((result, idx) => {
            const { team, passedMemos, unpassedMemos } = result;

            return (
              <div key={idx} style={{ marginBottom: '5px' }}>
                <div>
                  {`${passedMemos.length}/${tunnel.memos.length},\t${team.map(n => n.name).join(', ')}`}
                </div>
                <div>
                  {unpassedMemos.map((memo, idx) => {
                    const { type, reward: r, valueThresholds: v, skills: s, name } = memo;

                    return (
                      <div key={idx}>
                        {`(X) ${r[0]}*${r[1]} ${type}${v?v.map(a=>a.join(':')).join() : ''}${s?s.join(','):''} ${name}`}
                      </div>
                    );
                  })}
                </div>
              </div>
            );
          })}
        </Panel>
        <div className='run-placeholder'></div>
        <Button
          className='run'
          bsSize='large'
          bsStyle='success'
          onClick={() => this.onClickRun()}
        >
          <Glyphicon glyph='play' />
          執行測試
        </Button>
        <TunnelModal
          show={tOpened}
          showModal={val => this.showTModal(val)}
          allTunnels={allTunnels}
          selectedTunnel={tunnel}
          onSelectTunnel={tunnel => this.onSelectTunnel(tunnel)}
        />
        <NamekoModal
          show={nOpened}
          showModal={val => this.showNModal(val)}
          levels={levels}
          allNamekos={allNamekos}
          selectedNamekos={selectedNamekos}
          onClickLevel={level => this.onClickLevel(level)}
          onClick={nameko => this.onSelectNameko(nameko)}
          onClickAction={this.onClickNamekoAction.bind(this)}
        />
        <Modal
          animation={false}
          show={lOpened}
        >
          <Modal.Body>
            loading ...
            {this.useWorker && `${this.state.percentage}%`}
          </Modal.Body>
        </Modal>
      </div>
    );
  }
}

export default App;
