// react
import React, { Component } from 'react';

// react-bootstrap
import {
  Button,
  Clearfix,
  Modal,
} from 'react-bootstrap';

// custom
import NamekoEditor from './NamekoEditor';
import Nameko from './Nameko';

export default class extends Component {
  constructor(props) {
    super(props);
    this.state = {
      mode: 'list',
      nameko: null,
    };
  }

  showEditor(nameko) {
    this.setState({
      mode: nameko ? 'editor' : 'list',
      nameko: nameko || null,
    });
  }

  render() {
    const { mode, nameko } = this.state;
    const { show, showModal, levels, allNamekos, selectedNamekos, onClickLevel, onClick, onClickAction } = this.props;

    return (
      <Modal
        className='nameko-list-modal'
        show={show}
        onHide={() => {
          showModal(false);
          this.showEditor(false);
        }}
      >
        <Modal.Header>
          <Modal.Title>選擇菇菇</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          {mode === 'list' ? (<div>
            <div>
              {levels.map((level, idx) => (
                <Button
                  key={idx}
                  onClick={() => onClickLevel(level)}
                >
                  {level.name}
                </Button>
              ))}
            </div>
            <div>
              <Button onClick={() => {
                allNamekos.forEach(n => selectedNamekos.includes(n) || onClick(n));
              }}>
                全選
              </Button>
              <Button onClick={() => {
                allNamekos.forEach(n => selectedNamekos.includes(n) && onClick(n));
              }}>
                全不選
              </Button>
            </div>
            <div className='all-block'>
              {(() => {
                const comps = [];

                allNamekos.forEach((nameko, idx) => {
                  comps.push(<Nameko
                    key={idx}
                    nameko={nameko}
                    isSelected={selectedNamekos.includes(nameko)}
                    onClick={() => onClick(nameko)}
                    onClickEdit={() => this.showEditor(nameko)}
                  />);

                  if (idx % 3 === 2) {
                    comps.push(<Clearfix key={`_${idx}`} visibleXsBlock />);
                  }
                });

                return comps;
              })()}
            </div>
          </div>) : (<NamekoEditor
            nameko={nameko}
            onClick={onClickAction}
          />)}
        </Modal.Body>
        <Modal.Footer>
          <Button
            bsSize='large'
            bsStyle='primary'
            onClick={() => mode === 'list' ? showModal(false) : this.showEditor(false)}
          >
            完成
          </Button>
        </Modal.Footer>
      </Modal>
    );
  }
}
